.main-text {
  margin: auto;
  margin-top: 200px;
  width: 90%;
  height: 55vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 80px;
  font-weight: 700;
  color: var(--hero-text);
}
.main-text span {
  color: var(--hero-span);
  font-weight: 800;
  text-decoration: underline;
}

/* responsovness for 1300px*/
@media (max-width: 1300px) {
  .main-text {
    font-size: 72px;
    line-height: 144px;
  }
}
/* responsovness for 1100px*/
@media (max-width: 1100px) {
  .main-text {
    font-size: 64px;
    line-height: 128px;
  }
}
/* responsovness for 900px*/
@media (max-width: 900px) {
  .main-text {
    font-size: 56px;
    line-height: 112px;
  }
}
/* responsovness for 800px*/
@media (max-width: 800px) {
  .main-text {
    margin-top: 150px;
    font-size: 48px;
    line-height: 96px;
  }
  .main-text span {
    cursor: default;
  }
}
/* responsovness for 670px*/
@media (max-width: 670px) {
  .main-text {
    margin-top: 110px;
    font-size: 40px;
    line-height: 88px;
  }
}
/* responsovness for 500px*/
@media (max-width: 500px) {
  .main-text {
    height: 300px;
    margin-top: 100px;
    font-size: 28px;
    line-height: 56px;
  }
}
/* responsovness for 410px*/
@media (max-width: 410px) {
  .main-text {
    margin-top: 80px;
    font-size: 26px;
    line-height: 60px;
  }
}
@media (max-width: 330px) {
  .main-text {
    margin-top: 70px;
    font-size: 20px;
    line-height: 40px;
  }
}/*# sourceMappingURL=Hero.css.map */