body {
  background: transparent;
}

.swiper {
  width: 100%;
}
.swiper .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: transparent;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  width: 1100px;
  height: 900px;
}
.swiper .swiper-slide .NewWifu {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.swiper .swiper-slide .NewWifu img {
  border-radius: 90px;
  display: block;
  position: absolute;
  width: 1100px;
  height: 600px;
  -o-object-fit: cover;
     object-fit: cover;
  box-shadow: 0px 0px 95px 0px rgba(0, 0, 0, 0.43);
  transition: 0.2s ease-in-out;
}
.swiper .swiper-slide .NewWifu img:hover {
  transform: scale(1.05);
  filter: grayscale(70%);
}

/* responsovness for 1300px*/
@media (max-width: 1300px) {
  .swiper .swiper-slide img {
    border-radius: 72px;
    width: 880px;
    height: 480px;
  }
}
