  }
}
/* responsovness for 410px*/
@media (max-width: 410px) {
  .about .container h1 {
    font-size: 16px;
  }
  .about .container p {
    font-size: 12px;
    line-height: 32px;
  }
}/*# sourceMappingURL=About.css.map */